import React from 'react';
import axios from "axios";
import Movie from "./Movie";
import "./App.css";
// import PropTypes from "prop-types";

class App extends React.Component{
  state = {
    isLoading: true,  //순서2. true
    movies: []
  };

  //순서6. axios.get() 실행, 시간이 필요하기 때문에 await를 넣음 (async를 사용해야 await를 사용할 수 있음)
  //       jS에게 getMovies 함수에게 시간이 필요하고 기다려야 한다고 말하는 것
  getMovies = async () =>{  //async: 이 함수가 비동기라고 한 것, "너는 이걸 기다려야해"라는 말
    const { data: { data: { movies }}} = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");  //await: 기다리길 원하는 대상=axios
    this.setState({ movies, isLoading: false });   //state의 movies에 axios를 통해 얻은 movies를 넣음    //axios가 끝날 때까지 기다렸다가 게속해
  }                                                //isLoading의 상태 변경    =>refreshing                               

  componentDidMount(){  //순서4. component가 마운트 되자마자 호출 됨
    this.getMovies();   //순서5. getMovies()함수 실행

    // setTimeout(() => {
    //   this.setState({ isLoading: false });
    // }, 6000);
  }

  render() {
    const { isLoading, movies } = this.state; //순서1. isLoading 상태 확인
    return (
    <section className="container">
      {isLoading ? (
        <div className="loader">
            <span className="loader__text">Loading...</span>
        </div>
      ) : (
        <div className="movies">
          {movies.map(movie => (
            <Movie 
              key={movie.id}
              id={movie.id}
              year={movie.year}
              title={movie.title}
              summary={movie.summary}
              poster={movie.medium_cover_image}
              genres={movie.genres}
            />
          ))}
        </div>
      )}
    </section>  //순서3. Loding... , application이 마운트 됨 //순서7. movies.map()  =>rendering
    );
  }
//   state = {
//     count: 0
//   };

//   add = () => {
//     this.setState(current => ({ count: current.count + 1 }));
//   };
//   minus = () => {
//     this.setState(current => ({ count: current.count - 1 }));
//   };

//   render(){
//     return (
//       <section>
//         <h1>The number is: {this.state.count}</h1>;
//         <button onClick={this.add}>Add</button>
//         <button onClick={this.minus}>Minus</button>
//       </section>
//     );
//   }
}

export default App;
